<template>
	<view class="box">
		<view class="address-box">
			<view class="address-li">
				<view class="text">收货人</view>
				<input v-model="consignee" class="input" type="text" placeholder="请填写" />
			</view>
			<view class="address-li">
				<view class="text">手机号</view>
				<input v-model="mobile" class="input" maxlength="11" type="text" placeholder="请填写" />
			</view>
			<view class="address-li" @tap="regionVisible=true">
				<view class="text">所在地区</view>
				<input v-model="address1" class="input" type="text" placeholder="请选择" disabled />
			</view>
			<view class="address-li long-line">
				<view class="text">详细地址</view>
				<input v-model="address2" class="input" type="text" placeholder="请填写，如街道、楼栋、单元等等" />
			</view>
		</view>
		<view class="address-box">
			<view class="address-li" @tap="addressDefault">
				<view class="text">设为默认</view>
				<view class="iconfont iconlujing " :class="{'iconxuanzhong':isDefaultAddress==100}"></view>
			</view>
		</view>
		<view class="address-box" @tap='deleteAddress' v-if='seqId'>
			<view class="address-li">
				<view class="text" style="width: 100%;color: red;">删除收货地址</view>
			</view>
		</view>
		<view class="blank" :class="{'padd-safe':shapedScreen}"></view>
		<view class="fix-box" :class="{'padd-safe':shapedScreen}">
			<view class="bg-btn" @tap="addressAdd">
				确定
			</view>
		</view>
		<w-picker
			:visible.sync="regionVisible"
			mode="region"
			:value="regionNumber"
			default-type="value"
			:hide-area="false"
			@confirm="cityConfirm($event)"
			@cancel="onCancel"
			ref="region" 
		></w-picker>

	</view>
</template>

<script>
	import wPicker from "@/components/w-picker/w-picker.vue"
	export default {
		data() {
			return {
				address1: "",
				address2: "",
				consignee: "",
				customerSeqId: this.$common.getLocalSync('customerSeqId'),
				isDefaultAddress: 99,
				mobile: "",
				regionNumber: ["110000", "110100", "110101"], //默认选中的地址
				seqId: "",
				regionVisible:false,
				shapedScreen:this.shapedScreen
			}
		},
		components: {
			wPicker
		},
		onLoad(option) {
			this.seqId = option.seqId;
			if (this.seqId) {
				this.loadAdressDetail()
			}
		},
		methods: {
			deleteAddress() {
				let self=this;
				uni.showModal({
					title: "温馨提示",
					content: "确认删除地址？删除后不可恢复",
					success(res) {
						if (res.confirm) {
							self.$api.get('/o2oShippingAddress/delete', {
								params: {
									seqId: self.seqId
								}
							}).then(res => {
								if (res.code == 200) {
									self.$box.toast(res.msg);
									uni.navigateBack()
								}
							})
						}
					}
				})

			},
			showPicker() {
				console.log(111);
				this.$refs['region'].show();
			},
			cityConfirm(event) {
				this.address1 = event.result;
				this.regionNumber = event.value
			},
			// 获取地址详情
			loadAdressDetail() {
				this.$api.get('/o2oShippingAddress/getByPk', {
					params: {
						seqId: this.seqId
					}
				}).then(res => {
					if (res.code == 200) {
						let address_detail = res.data;
						this.address1 = address_detail.address1;
						this.address2 = address_detail.address2;
						this.consignee = address_detail.consignee;
						this.customerSeqId = address_detail.customerSeqId;
						this.isDefaultAddress = address_detail.isDefaultAddress;
						this.mobile = address_detail.mobile;
					}
				}).catch(err => {
					console.log(err);
				})
			},
			// 增加更新地址
			addressAdd() {
				let phonereg = /^1[3456789]\d{9}$/
				if (!(phonereg.test(this.mobile))) {
					this.$box.toast('请填写正确的手机号码', 'none');
					return;
				}

				if (!this.address1 || !this.address2 || !this.consignee || !this.mobile) {
					this.$box.toast('请正确填写所有信息');
					return
				}
				let url = '/o2oShippingAddress/insert'
				let obj = {
					address1: this.address1,
					address2: this.address2,
					consignee: this.consignee,
					customerSeqId: this.customerSeqId,
					isDefaultAddress: this.isDefaultAddress,
					mobile: this.mobile,
					regionNumber: this.regionNumber.join(','),
				}
				if (this.seqId) {
					// 更新 编辑
					url = "/o2oShippingAddress/update";
					obj = {
						address1: this.address1,
						address2: this.address2,
						consignee: this.consignee,
						customerSeqId: this.customerSeqId,
						isDefaultAddress: this.isDefaultAddress,
						mobile: this.mobile,
						regionNumber: this.regionNumber.join(','),
						seqId: this.seqId
					}
				}
				this.$api.post(url, obj).then(res => {
					if (res.code == 200) {
						uni.navigateBack()
					} 
				})
			},
			addressDefault() {
				this.isDefaultAddress = this.isDefaultAddress == 100 ? 99 : 100
			}

		}
	}
</script>

<style lang="scss">
	.fix-box .bg-btn {
		width: 100%;
	}

	.address-box {
		width: 708rpx;
		background: rgba(255, 255, 255, 1);
		border-radius: 14rpx;
		margin: 30rpx auto;
		font-size: 30rpx;

		.address-li {
			display: flex;
			justify-content: space-between;
			align-items: center;
			width: 650rpx;
			padding: 0 26rpx;
			min-height: 90rpx;
			line-height: 90rpx;
			border-bottom: 1px solid #eee;

			.text {
				width: 130rpx;
				flex-shrink: 0;
			}

			.input {
				flex-grow: 1;
				text-align: right;
				height: 60rpx;
				color: #333;
			}
		}

		.long-line {
			flex-wrap: wrap;

			.text {
				width: 650rpx;
			}

			.input {
				text-align: left;
				margin-bottom: 40rpx;
			}

		}
	}

	.iconlujing {
		font-size: 40rpx;
	}

	.iconxuanzhong {
		color: $color-money;
	}
</style>
